import React from "react";
import { parseJobLog, parseJobLogNoTimestamp } from "components/utils/utils";
import { Job, JobLog, JobStatus, JobStreamOutput } from "types";
import { useQuery } from "react-query3";
import { CodeEditor, FullEditor } from "components/ui/Editor/Editor";
import { EditorProps } from "@monaco-editor/react";
import { Skeleton } from "antd";
import TimestampToggle from "components/ui/TimestampToggle";

const config: EditorProps["options"] = {
  readOnly: true,
  wordWrap: "on",
  wrappingIndent: "same",
  minimap: {
    enabled: false,
  },
  lineHeight: 20,
  lineNumbersMinChars: 5,
  scrollBeyondLastLine: false,
  scrollbar: {
    horizontal: "hidden",
    vertical: "hidden",
    useShadows: false,
  },
  hideCursorInOverviewRuler: true,
  overviewRulerLanes: 0,
  overviewRulerBorder: false,
};

export default function JobOutput({ showTimestamp, setShowTimestamp, refetchInterval, job, hideToolbar }: { showTimestamp?: boolean, setShowTimestamp?: () => void, refetchInterval: false | number, job: Job, hideToolbar?: boolean }) {
  const { data, isLoading, refetch } = useQuery<JobLog>(
    `/job/${job?.id}/log`, {
    refetchOnWindowFocus: false,
    keepPreviousData: true,
    refetchInterval
  }
  );

  function getLogContent() {
    if (isLoading) return null;
    try {
      let logContent: JobStreamOutput[] = JSON.parse(data.log)?.map((entry: JobStreamOutput) => ({ ...entry, JobId: data?.id }));
      return logContent;
    } catch (error) {
      //console.error("Error parsing Job Log");
    }
  }

  const content = getLogContent();
  let jobLogContent = "";
  if ((job?.status === JobStatus.Completed || job?.status === JobStatus.Failed) && content == null && !isLoading) {
    jobLogContent = "# This job produced no output"
  } else {
    jobLogContent = showTimestamp ? parseJobLog(getLogContent()) : parseJobLogNoTimestamp(getLogContent());
  }


  return (
    <CodeEditor refetch={refetch}>
      <FullEditor
        title="Output"
        language="psulog"
        hideToolbar={hideToolbar}
        showSave={false}
        actions={<TimestampToggle isVisible={showTimestamp} setIsVisible={() => setShowTimestamp()} />}
        loading={
          <Skeleton
            active
            loading={isLoading}
            paragraph={{ rows: 5, width: [60, 60, 40, 80, 120] }}
          />
        }
        options={{ ...config }}
        value={jobLogContent}
      />
    </CodeEditor>
  );
}
