import React from "react";
import { Switch } from "antd";
import { presetPrimaryColors } from "@ant-design/colors";

export default function TimestampToggle({
  isVisible,
  setIsVisible,
}: {
  isVisible: boolean;
  setIsVisible: () => void;
}) {
  return (
    <Switch
      size="small"
      style={{
          backgroundColor: isVisible ? presetPrimaryColors["blue"] : presetPrimaryColors["red"]
      }}  
      checkedChildren="Show Time"
      unCheckedChildren="Hide Time"
      checked={isVisible}
      onChange={setIsVisible}
    />
  );
}
